export default function getStratosphereErrorPaths(partnerId) {
  return {
    error: () => `/${partnerId}/error`,
    orderError: () => `/${partnerId}/order-error`,
    exportError: () => `/${partnerId}/export-error`,
    systemError: () => `/${partnerId}/system-error`,
    alreadyLinked: () => `/${partnerId}/already-linked`,
    unauthorized: () => '/unauthorized',
    needAssistance: () => `/${partnerId}/need-assistance`,
  };
}
