import React from 'react';
import './toast-notifications.scss';

class ToastNotification extends React.Component {
  constructor(props) {
    super();
    this.state = {
      notifObject: []
    };
  }

  setNotification(val) {
    this.state.notifObject.push({
      key: val.key,
      message: val.message,
      type: val.type
    });
    setTimeout(() => {
      if (this.state.notifObject) {
        let notification_obj = this.state.notifObject.filter(
          (item) => item.key !== val.key
        );
        this.setState({ notifObject: notification_obj });
      }
    }, 5000);
  }

  componentDidUpdate(prevProps) {
    let data = this.props.toastData;
    let lastData = prevProps.toastData;
    if (data.key && data.key != lastData.key) {
      this.setNotification(data);
    }
  }

  render() {
    return (
      <>
        {this.state.notifObject.length > 0 && (
          <ul className='pf-c-alert-group pf-m-toast'>
            {this.state.notifObject.map((t) => (
              <li className='pf-c-alert-group__item' key={'toast-' + t.key}>
                <div className={'pf-c-alert pf-m-' + t.type}>
                  <div className='pf-c-alert__icon'>
                    {t.type === 'success' && (
                      <i className='fas fa-check-circle' aria-hidden='true'></i>
                    )}
                    {t.type === 'danger' && (
                      <i className='fas fa-info-circle' aria-hidden='true'></i>
                    )}
                    {t.type === 'info' && (
                      <i className='fas fa-info-circle' aria-hidden='true'></i>
                    )}
                  </div>
                  <h4 className='pf-c-alert__title'>
                    <div className='pf-c-alert__description'>
                      <p>{t.message}</p>
                    </div>
                  </h4>
                </div>
              </li>
            ))}
          </ul>
        )}
      </>
    );
  }
}

export default ToastNotification;
