import i18n from 'i18next';

import * as partner_aws from './locales/partner_aws/index';
import * as partner_azure from './locales/partner_azure/index';
import LanguageDetector from 'i18next-browser-languagedetector';
import { initReactI18next } from 'react-i18next';
import detector from 'i18next-browser-languagedetector';
import commonTranslationEN from './locales/en/common.js';
import constants from './app/component/shared/constants.js';

const resources = {
  en: {
    aws: partner_aws.translationAWSEN,
    azure: partner_azure.translationAzureEN,
    common: commonTranslationEN,
  },
};
i18n
  .use(detector)
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    fallbackLng: 'en',
    order: ['querystring', 'cookie', 'localStorage', 'sessionStorage', 'navigator', 'htmlTag', 'path', 'subdomain'],
    resources,
    debug: false,
    languages: ['en'],
    react: {
      useSuspense: false,
    },
    interpolation: {
      escapeValue: false,
    },

    ns: constants.namespaces,

    fallbackNS: 'common',
  });

export default i18n;
