import { Button, Checkbox, FormGroup, FormSelect, FormSelectOption, Text, TextContent, TextVariants } from '@patternfly/react-core';
import { ExternalLinkAltIcon } from '@patternfly/react-icons';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import langList from '../../../locales/language.json';

function TermsFormGroup(props) {
  const { local, onAgreeToTerms, onSelectTermsId, terms } = props;
  const downloadURLs = terms[0]?.translations;

  const [selectedLanguage, setSelectedLanguage] = useState(getDefaultLanguage());
  const [selectedTerms, setSelectedTerms] = useState(getDefaultTerms(terms));
  const [isAgreedToTerms, setIsAgreedToTerms] = useState(false);

  const findTermsForLanguage = (languageCode) => {
    return downloadURLs.find((downloadURL) => downloadURL.localeCode === languageCode);
  };
  const handleSelectLanguage = (languageCode) => {
    let matchingLanguage = langList.find((lang) => lang.code === languageCode);
    setSelectedLanguage(matchingLanguage);

    let matchingTerms = findTermsForLanguage(languageCode);
    setSelectedTerms(matchingTerms);
    onSelectTermsId(matchingTerms.id);
  };
  const handleAgreeCheckbox = (checked) => {
    setIsAgreedToTerms(checked);
    onAgreeToTerms(checked);
  };
  const downloadTerms = () => {
    window.open(selectedTerms.downloadUrl, '_blank');
  };

  return (
    <>
      <FormGroup label="Terms and conditions *">
        <FormSelect value={selectedLanguage.code} onChange={handleSelectLanguage} aria-label="FormSelect Input">
          {langList.map((lang, index) => (
            <FormSelectOption key={index} value={lang.code} label={lang.nativeName} />
          ))}
        </FormSelect>
        <Checkbox
          id="agree-check"
          isChecked={isAgreedToTerms}
          className="pf-u-mt-md"
          label={
            <>
              {local('link-account.terms')}
              <Button variant="link" isInline onClick={downloadTerms}>
                {local('link-account.enterprise_agreement.download') + ` - ${selectedLanguage.nativeName}`}
                &nbsp;
                <ExternalLinkAltIcon />
              </Button>
            </>
          }
          onChange={handleAgreeCheckbox}
          aria-label="terms checkbox"
        />
      </FormGroup>
    </>
  );
}

function getDefaultLanguage() {
  return langList.find((lang) => lang.code === 'en_US') || langList?.[0] || null;
}

function getDefaultTerms(terms) {
  let defaultLanguage = getDefaultLanguage();
  let translations = terms?.[0]?.translations;
  return translations?.find((translation) => translation.localeCode === defaultLanguage.code);
}

TermsFormGroup.propTypes = {
  local: PropTypes.func.isRequired,
  onAgreeToTerms: PropTypes.func.isRequired,
  onSelectTermsId: PropTypes.func.isRequired,
  terms: PropTypes.array.isRequired,
};

export { TermsFormGroup, getDefaultTerms };
