import React from 'react';
import '@patternfly/react-core/dist/styles/base.css';
import { Card, CardTitle, CardBody, CardFooter } from '@patternfly/react-core';
import 'react-responsive-modal/styles.css';
import './stratosphere-notifications.scss';
import { Trans } from 'react-i18next';

function StratosphereNotification(props) {
  const { local } = props;

  return (
    <>
      <Card id="notification-card">
        {/* https://reactjs.org/docs/conditional-rendering.html#inline-if-with-logical--operator */}
        {props.headerKey && <CardTitle>{local(props.headerKey)}</CardTitle>}
        {props.bodyKey && <CardBody>{local(props.bodyKey)}</CardBody>}
        {props.footerKey && (
          <CardFooter>
            {/* See https://react.i18next.com/latest/trans-component */}
            {/* <1> in locale file: support link, must supply a href attribute */}
            <Trans i18nKey={props.footerKey}>
              Please return to your <a target="blank">marketplace console</a>
            </Trans>
          </CardFooter>
        )}
      </Card>
    </>
  );
}

export default StratosphereNotification;
