import PropTypes from 'prop-types';
import AwsDetails from './aws-details';
import AzureDetails from './azure-details';

function PartnerOrderDetails(partnerId, orderDetails, local) {
  switch (partnerId) {
    case 'aws':
      return new AwsDetails(orderDetails, local);
    case 'azure':
      return new AzureDetails(orderDetails, local);
    default:
      throw new Error(`Unknown partner: ${partnerId}`);
  }
}

PartnerOrderDetails.propTypes = {
  partnerId: PropTypes.string.isRequired,
  orderDetails: PropTypes.object,
  local: PropTypes.func.isRequired,
};

export default PartnerOrderDetails;
