import PropTypes from 'prop-types';

function AzureDetails(orderDetails, local) {
  return {
    getRows() {
      return [
        {
          label: local('review-order.customer_info.customer_number'),
          value: orderDetails?.azureCustomerId ?? '-',
        },
      ];
    },
  };
}

AzureDetails.propTypes = {
  orderDetails: PropTypes.object.isRequired,
  local: PropTypes.func.isRequired,
};

export default AzureDetails;
